/* ============== GENERAL ================ */


h1 {
    text-align: center;
}

.action_container {
    align-items: center;
    text-align: center;
}




/* ============== NAVBAR ============== */


body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .pages {
    height: 80vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    font-size: 40px;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 7%;
    background-color: #333;
  }
  
  .bars-button {
    display: none;
  }
  
  .logo {
    color: #fff;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 34px;
  }
  
  .links {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  
  .links a {
    text-decoration: none;
    color: #fff;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 400;
    padding: 10px 20px;
    position: relative;
  }
  
  .links a::after {
    position: absolute;
    content: '';
    bottom: 5px;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all .8s ease;
  }
  
  .links a:hover {
    color: #333;
    z-index: 1;
    transition: all .8 ease;
  }
  
  .links a:hover::after {
    height: 80%;
    z-index: -1;
  }



  /* ============= TABLE ============ */

.table_container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 1rem;
  }

  .table_box {
    margin-top: 6rem;
  }
  
  table {
    border-collapse: collapse;
    width: 100%;
  }
  
  th,
  td {
    border: 1px solid #ffffff;
    text-align: left;
    padding: 8px;
    font-size: 32px;
  }
  
  th {
    background-color: rgb(117, 201, 250);
  }
  
  td {
    background-color: rgb(205, 235, 253);
  }
  
  form {
    display: flex;
    gap: 5px;
  }
  
  form td:last-child {
    display: flex;
    justify-content: space-evenly;
  }
  
  form * {
    font-size: 28px;
  }


